body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url('https://cdn.pixabay.com/photo/2021/08/27/12/18/forest-6578551_960_720.jpg');
  background-size: cover;

}

.container {
  display:flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  gap: 1em;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

img {
  max-width: 90%;
  height:auto;
  margin: auto;
}

li {
  display: inline;
  margin: 5px;
}

.input-guess {
  display: flex;
  align-items: center;
  justify-content: center;



  width: 10%;
}

form {
  width: 50%
}

input {
  width: 100%;
  font-size: xx-large;
  font-weight: bold;
  text-align: center;
  border: none;
  margin: auto;
}

/* input {
  height: 100%;
} */

.popup {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(10px);
}

.popup-inner {
  position: relative;
  padding: 32px;
  width: 100%;
  max-width: 640px;
  opacity: 8px;
  background-color: white;
}




